import React from 'react';
import socket from '../socked/soked';

function Chat({ users, messages, userName, roomId, onAddMessage }) {
  const [messageValue, setMessageValue] = React.useState('');
  const messagesRef = React.useRef(null);

  const onSendMessage = () => {
    socket.emit('ROOM:NEW_MESSAGE', {
      userName,
      roomId,
      text: messageValue,
    });
    onAddMessage({ userName, text: messageValue });
    setMessageValue('');
  };

  React.useEffect(() => {
    messagesRef.current.scrollTo(0, 99999);
  }, [messages]);

  return (
    <div className="chat">
      <div className="chat-users" style={{ fontSize: 20 }}>
        room: <b style={{ fontSize: 20 }}>{roomId}</b>
        <hr />
        <b style={{ fontSize: 10 }}> ({users.length}):</b>
        <ul>
          {users.map((name, index) => (
            <li style={{ fontSize: 10 }} key={name + index}>
              {name}
            </li>
          ))}
        </ul>
      </div>
      <div className="chat-messages">
        <div ref={messagesRef} className="messages">
          {messages.map((message, index) => (
            <div
              key={message + index}
              style={{ fontSize: 10 }}
              className="message"
            >
              <p>{message.text}</p>
              <div>
                <span style={{ fontSize: 10 }}>{message.userName}</span>
              </div>
            </div>
          ))}
          <div style={{ fontSize: 10 }} className="message">
            users
          </div>
        </div>
        <form>
          <textarea
            value={messageValue}
            onChange={e => setMessageValue(e.target.value)}
            className="form-control"
            rows="3"
          ></textarea>
          <button
            onClick={onSendMessage}
            type="button"
            className="btn btn-primary"
          >
            Send
          </button>
        </form>
      </div>
    </div>
  );
}

export default Chat;
